.appCoontainer {
  width: 80%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 25% auto;
  border-radius: 15px;
  background-color: white;
  overflow: hidden;

  padding: 0;
}

@media (min-width: 536px) {
  .appCoontainer {
    margin: 8% auto;
  }
}
