.itemContainer {
  width: 100%;

  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  background-color: #f8f9f9;
  box-shadow: 2px 2px 5px #aaaaaa;

  margin-bottom: 15px;
  padding: 5px;
  border-radius: 10px;
}

.itemContainer p {
  width: 50%;
  max-width: 80%;
  text-align: center;
  word-wrap: break-word;
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
}

.Icon-check {
  color: black;
}

.Icon-check--active {
  color: green;
}

.TodoItem-p {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 1rem;
}

.TodoItem-p--complete {
  background-color: #52be80;
}

.Icon-delete:hover {
  color: red;
}

@media (min-width: 514px) {
  .itemContainer {
    max-width: 650px;
  }
}
